    title: "Natural Frames",
    linksrc: ``,
    content:
      "Natural framing is one the composition techniques which is widely known and is popular in landscape photography when you use an element within your image to frame the subject. This draws the eye into the photo and highlights the actual subject. These objects add a sense of depth and also help identify the environment in which the image was created. This internal frame can be constructed using a multitude of things you’ll find anywhere – branches, archways, tunnels, door frames etc., as well as things that aren’t solid like light, shadows, rain, fog, etc. – it doesn’t matter what you use.",
