    imgsrc: `https://ik.imagekit.io/satyamimages/mypics/tr:w-300/timefreeze_KRDM7824uHY.jpeg?updatedAt=1631160229290`,
    title: "High Speed Photography",
    linksrc: ``,
    content:
      "High-speed photography is capturing the moments that happen in a fraction of time which you can’t see with the naked eye, like a bursting balloon or a splash of water. High-speed photography has ability to manipulate the exposure of a photograph so that all movement is frozen and things can be seen that could not be seen with the eye alone. ",
