    imgsrc: `https://ik.imagekit.io/satyamimages/mypics/tr:w-300/silhouette_4TTwGREwTnf.jpeg?updatedAt=1631160228136`,
    title: "Silhouette",
    linksrc: ``,
    content:
      "A silhouette is a solid, dark image of a subject against a brighter background. Silhouette pictures usually show the subject in profile. It instantly grab your attention because of the contrast between dark and light. They share emotion and mystery which pulls the viewer in deeper to create a more meaningful connection to the image. The best time of day for silhouette shooting is either early in the morning or late in the day, when the sun is on the horizon. That way, you’re more likely to capture a warm-colored sky, and there won’t be too much light brightening up your subject.",
